<template>
  <h1>Egresos ---------------{{getSuma}}</h1>
  <hr/>
  
  <div v-if="getEditEgreso">

    <EditEgreso/>
  </div>
  <div v-else class="row">
    <FormEgreso v-if="getPerfil==='admin'" class="col-sm-12 col-md-6 col-lg-3 col-xl-3 mt-5"/>
    <TablaEgresos class="col-sm-12 col-md-6 col-lg-9 col-xl-9 mt-3"  />
  </div>
</template>

<script>
import { defineAsyncComponent } from 'vue';
import { mapGetters } from 'vuex';

export default {
    components: {
    FormEgreso: defineAsyncComponent(() => import("@/modules/socios/components/Egreso/FormEgreso")),
    TablaEgresos: defineAsyncComponent(() => import("@/modules/socios/components/Egreso/TablaEgresos")),
    EditEgreso: defineAsyncComponent(() => import("@/modules/socios/components/Egreso/EditEgreso")),
  },
  computed: {
    ...mapGetters('egreso',['getEditEgreso','getSuma']) ,
    ...mapGetters('auth',['getPerfil'])
  }
}
</script>

